body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Typography */
/* Base font size */
html {
  font-size: 16px;
}

.left-container > * {
width:auto;
margin-bottom:2em;
}

.right-container > * {
width:auto;
margin-bottom:1em;
}

.app {
  display: flex;
  flex-direction: column;
  padding: 0.8em;
  align-items: center;
  justify-content: center;
  background-color: #f0f0f0;

}

@media (min-width: 768px) {
  .app {
    padding:2em;
    flex-direction: row;
    align-items:flex-start;
  }

  .app > * {
    width: 50%;
  }

  .left-container {
    margin-right: 1em;
  }
}

/* Custom Classes for Specific Elements */
.experience-list {
  list-style: none; 
  margin-bottom: 1em;
}

.experience-item {
  margin-bottom: 1.5em; 
}

.experience-item li {
  margin-bottom: 0.25em; 
}

.experience-title {
  font-size: 1.2em;
  font-weight: bold;
  color: #333; 
}

.experience-company {
  font-style: italic; 
}

.experience-description {
  margin-top: 0.5em;
  text-align: justify; 
}

.highlighted-info {
  background-color: #faecaf; 
  color: #000000; 
  padding: 1em;
  border: 1px solid #000;
  margin-top: 1em;
  margin-bottom: 1em;
}

.testimonials-paragraph {
  padding-bottom: 0.5em;;
}

.projects-item {
  padding: 1.2em; 
}

.projects-title {
  font-size: 1.2em; 
  font-weight: bold; 
  color: #333; 

}

.projects-description {
  margin-top: 0.5em; 
  text-align: justify;

}


.projects-outcome {
  list-style: inside; 
  color: #666; 

}


.content-container {
padding: 1em;
}

.projectsitems-container {
  margin-bottom: 2.5em;
}